.clearfix {
  *zoom: 1;
  clear: both;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.full-width {
  margin-left: calc(50% - 49vw);
  margin-right: calc(50% - 49vw);
}

.colors--wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.color--info {
  text-align: center;
}

.color--info div {
  width: 4rem;
  height: 4rem;
  box-shadow: 0 1px 2px rgba(0,0,0,.34),0 1px 3px rgba(0,0,0,.22);
  transition: .3s ease;
}

.color--info div:hover {
  box-shadow: 0 3px 6px rgba(0,0,0,.2),0 10px 20px rgba(0,0,0,.25);
  transform: scale(1.12);
}

.color--info .prime {
  width: 6rem;
  height: 6rem;
}

